import i18n from "@/i18n";
import $axios from "axios";
import $ from "jquery";
import {message as $message} from "ant-design-vue";

var util = {
    alicdnIcon : "//at.alicdn.com/t/font_2031063_1mutltdz0v2.js",
    isNull: function (obj) {
        if (obj == null) {
            return true;
        }
        if (obj == undefined) {
            return true;
        }
        if (obj == "") {
            return true
        }
        if (obj.length == 0) {
            return true;
        }
        if (obj >= 0){
            return true;
        }
        return false;
    },
    sampletypes : function (sampletype){
        var array = new Array();
        if (sampletype == "01"){
            /**  */
            array.push({key:"01",val:"DNA"});
            array.push({key:"02",val:"total RNA"});
            array.push({key:"03",val:"PCR"});
            array.push({key:"04",val:"chip-seq DNA"});
            array.push({key:"05",val:""});
            array.push({key:"06",val:"FFPE"});
            array.push({key:"00",val:""});
        }else if (sampletype == "02"){
            /**  */
            array.push({key:"21",val:""});
            array.push({key:"22",val:""});
            array.push({key:"23",val:""});
            array.push({key:"24",val:""});
            array.push({key:"25",val:""});
            array.push({key:"26",val:""});
            array.push({key:"20",val:""});
        }else if (sampletype == "03"){
            /**  */
            array.push({key:"41",val:""})
            array.push({key:"40",val:""})
        }
        return array;
    },
    sampleStatu : function (sampletype){
        var array = new Array();
        if (sampletype == "01"){
            /**  */
            array.push({key:"01",val:""});
            array.push({key:"02",val:"TE"});
            array.push({key:"03",val:"Rnase"});
            array.push({key:"04",val:"EB"});
            array.push({key:"05",val:""});
            array.push({key:"00",val:""});
        }else if (sampletype == "02"){
            /**  */
            array.push({key:"21",val:""});
            array.push({key:"22",val:""});
            array.push({key:"23",val:"RNAlater"});
            array.push({key:"24",val:"Trlzol"});
            array.push({key:"20",val:""});
        }else if (sampletype == "03"){
            /**  */
            array.push({key:"41",val:""});
            array.push({key:"42",val:""});
            array.push({key:"40",val:""});
        }
        return array;
    },
    cellSortMethods : function (){
        return[
            {key:"01",val:""},
            {key:"02",val:""},
            {key:"03",val:""},
            {key:"04",val:"BD"},
            {key:"05",val:"NanoCellect "}
        ]
    },
    databaseTypes : function (sampletype){
        var array = new Array();
        if (sampletype == "01"){
            /**  */
            array.push({key:"01",val:"DNA"});
            array.push({key:"02",val:"DNA"});
            array.push({key:"03",val:""});
            array.push({key:"04",val:"PCR-free"});
            array.push({key:"05",val:""});
            array.push({key:"06",val:""});
            array.push({key:"07",val:"LncRNA"});
        }else if (sampletype == "02"){
            /**  */
            array.push({key:"21",val:"10X"});
            array.push({key:"22",val:"10X"});
            array.push({key:"23",val:"10X ATAC"});
            array.push({key:"24",val:"smart-seq"});
            array.push({key:"25",val:"ATAC"});
            array.push({key:"26",val:"HI-C"});
            array.push({key:"27",val:"DNA"});
            array.push({key:"28",val:"DNA"});
            array.push({key:"29",val:""});
            array.push({key:"30",val:"PCR-free"});
            array.push({key:"31",val:""});
            array.push({key:"32",val:""});
            array.push({key:"33",val:"LncRNA"});
        }else if (sampletype == "03"){
            /**  */
            array.push({key:"41",val:"10X"});
            array.push({key:"42",val:"10X ATAC"});
            array.push({key:"43",val:"smart-seq"});
            array.push({key:"44",val:"ATAC"});
            array.push({key:"45",val:"HI-C"});
        }
        return array;
    },
    seqPlants : function (){
        var array = new Array();
        array.push({key:"01",val:"Hiseq-SE50"});
        array.push({key:"02",val:"Hiseq-PE150"});
        array.push({key:"03",val:"Hiseq-PE250"});
        array.push({key:"00",val:""});
        return array;
    },
    extractmethods : function (){
        var array = new Array();
        array.push({key:"01",val:""});
        array.push({key:"02",val:""});
        array.push({key:"00",val:""});
        return array;
    },
    checkresults : function (){
        var array = new Array();
        array.push({key:"01",val:""});
        array.push({key:"02",val:""});
        array.push({key:"03",val:""});
        return array;
    },
    sampleInits : function (){
        var array = new Array();
        array.push({key:"01",val:i18n.t("nucleicAcid") + i18n.t("sample")});
        array.push({key:"02",val:i18n.t("tissue") + i18n.t("sample")});
        array.push({key:"03",val:i18n.t("cell") + i18n.t("sample")});
        return array;
    },
    commonUploadUrl : function (){
        return $axios.defaults.baseURL + '/file/import/uploadFile';
    },
    commonHandleUploadChange : function (ret){
        if (ret.file.status == "uploading"){
            $("#submitting").modal("show");
        }else {
            $("#submitting").modal("hide");
            if (ret.file.status == "error"){
                $message.error(i18n.t("systemErr"));
            }else if (ret.file.status == "done"){
                window.location.reload();
            }
        }
    },
    downLoad : function (postData,url,fileName){
        $axios({
            url : url,
            data : postData,
            method : 'post',
            responseType : 'blob'
        }).then(function (res){
            let data = res.data;
            if (!data) {
                return
            }
            let url = window.URL.createObjectURL(new Blob([data]))
            let a = document.createElement('a')
            a.style.display = 'none'
            a.href = url
            a.setAttribute('download', fileName);
            document.body.appendChild(a)
            a.click() //
            window.URL.revokeObjectURL(a.href)
            document.body.removeChild(a)
        })
    }
}

export default util;